html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Gotham";
  src: url(../fonts/GothamPro.eot);
  src: url(../fonts/GothamPro.woff);
  src: url(../fonts/GothamPro.ttf);
  font-style: normal;
  font-weight: 400;
}

.accordion {
  display: flex;
  background: url("../img/bg/active-bg.png") no-repeat;
  background-size: cover;
  overflow: hidden;
  height: 668px;
}

@media only screen and (max-width: 700px) {
  .accordion {
    height: auto;
  }
}

@media only screen and (max-width: 700px) {
  .accordion {
    flex-direction: column;
  }
}

.accordion__item {
  width: 14%;
  transition: width 0.5s ease-out;
  transform: skew(-10deg);
  color: #363636;
  padding: 70px 40px 0;
}

@media only screen and (max-width: 1400px) {
  .accordion__item {
    padding: 40px 20px;
  }
}

@media only screen and (max-width: 1024px) {
  .accordion__item {
    transform: none;
  }
}

@media only screen and (max-width: 700px) {
  .accordion__item {
    width: 100%;
    padding: 20px;
  }
}

.accordion__item--open {
  width: 44%;
  transition: width 1s ease-out;
  background: transparent !important;
}

@media only screen and (max-width: 400px) {
  .accordion__item--open {
    padding: 0 10px;
  }
}

.accordion__item--open .accordion__title {
  visibility: hidden;
  opacity: 0;
}

.accordion__item--open .accordion--open {
  visibility: visible;
  opacity: 1;
  line-height: 28px;
  text-align: left;
  padding: 0 50px;
  transition: opacity 0.4s linear 0.7s;
}

@media only screen and (max-width: 1024px) {
  .accordion__item--open .accordion--open {
    transform: none;
    padding: 10px 0;
  }
}

@media only screen and (max-width: 700px) {
  .accordion__item--open .accordion--open {
    height: 350px;
    transition: height 0.5s linear;
  }
}

@media only screen and (max-width: 650px) {
  .accordion__item--open .accordion--open {
    height: 400px;
  }
}

@media only screen and (max-width: 400px) {
  .accordion__item--open .accordion--open {
    height: 500px;
  }
}

.accordion__item--open.accordion__item1:before, .accordion__item--open.accordion__item5:before {
  display: none;
}

.accordion--open {
  visibility: hidden;
  opacity: 0;
  line-height: 0;
  transform: skew(10deg);
  height: 0;
}

.accordion__item1 {
  position: relative;
}

.accordion__item1:before {
  content: '';
  background: url("../img/bg/1.png") no-repeat;
  background-size: cover;
  width: 200%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
}

@media only screen and (max-width: 700px) {
  .accordion__item1:before {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .accordion__item1 {
    background: url("../img/bg/1.png") no-repeat;
    background-size: cover;
  }
}

.accordion__item2 {
  background: url("../img/bg/2.png") no-repeat;
  background-size: cover;
}

.accordion__item3 {
  background: url("../img/bg/4.png") no-repeat;
  background-size: cover;
}

.accordion__item4 {
  background: url("../img/bg/3.png") no-repeat;
  background-size: cover;
}

.accordion__item5 {
  position: relative;
}

.accordion__item5:before {
  content: '';
  background: url("../img/bg/4.png") no-repeat;
  background-size: cover;
  width: 300%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media only screen and (max-width: 700px) {
  .accordion__item5:before {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .accordion__item5 {
    background: url("../img/bg/1.png") no-repeat;
    background-size: cover;
  }
}

.accordion__title {
  font-family: 'Gotham';
  font-size: 50px;
  color: #808080;
  text-align: center;
  transform: skew(10deg) rotate(-80deg) translate(-450px, 0px);
  white-space: nowrap;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.5s linear 0.3s;
}

@media only screen and (max-width: 1024px) {
  .accordion__title {
    transform: rotate(-90deg) translate(-500px, 0px);
  }
}

@media only screen and (max-width: 700px) {
  .accordion__title {
    transform: none;
  }
}

@media only screen and (max-width: 600px) {
  .accordion__title {
    font-size: 30px;
  }
}

@media only screen and (max-width: 350px) {
  .accordion__title {
    white-space: normal;
  }
}

.accordion__title--open {
  font-family: 'Gotham';
  font-size: 55px;
  padding-bottom: 50px;
  line-height: 53px;
}

@media only screen and (max-width: 1250px) {
  .accordion__title--open {
    font-size: 40px;
  }
}

@media only screen and (max-width: 900px) {
  .accordion__title--open {
    font-size: 30px;
    padding-bottom: 20px;
    line-height: 33px;
  }
}

@media only screen and (max-width: 700px) {
  .accordion__title--open {
    text-align: center;
  }
}

.accordion__subtitle {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 16px;
}

.accordion__text {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 16px;
  font-weight: 300;
  padding: 30px 0;
}

@media only screen and (max-width: 1100px) {
  .accordion__text {
    padding: 10px 0;
  }
}

.accordion__text-item {
  display: inline-block;
  width: 100%;
}

.accordion__text-item:before {
  content: '';
  display: inline-block;
  background: #1c7fff;
  width: 7px;
  height: 7px;
  border-radius: 7px;
  margin-right: 10px;
}

.container {
  max-width: 1215px;
  margin: 0 auto;
}

@media only screen and (max-width: 1250px) {
  .container {
    padding: 0 20px;
  }
}

.clients {
  position: relative;
  padding: 88px 0 98px;
  color: #363636;
}

.clients__inner {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1024px) {
  .clients__inner {
    flex-wrap: wrap;
  }
}

.clients__review {
  width: 40%;
}

@media only screen and (max-width: 850px) {
  .clients__review {
    text-align: center;
    width: 100%;
  }
}

.clients__title {
  font-family: "Gotham";
  font-size: 50px;
}

@media only screen and (max-width: 420px) {
  .clients__title {
    font-size: 30px;
  }
}

.clients__text {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 33px;
  padding: 50px 0 44px;
  line-height: 43px;
}

.clients__text--weight {
  font-weight: 500;
}

@media only screen and (max-width: 420px) {
  .clients__text {
    font-size: 20px;
  }
}

.clients__button {
  font-family: "Gotham";
  background: #1c7fff;
  width: 239px;
  padding: 25px 0 26px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  color: #ffffff;
}

.clients__button:hover {
  background: rgba(28, 127, 255, 0.9);
}

@media only screen and (max-width: 1024px) {
  .clients__button {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: 14px;
  }
}

.clients__icons {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 60%;
  justify-content: space-between;
}

@media only screen and (max-width: 850px) {
  .clients__icons {
    width: 100%;
  }
}

@media only screen and (max-width: 530px) {
  .clients__icons {
    flex-wrap: wrap;
    justify-content: space-around;
  }
}

.clients__img {
  width: 33%;
  text-align: center;
}

@media only screen and (max-width: 1024px) {
  .clients__img {
    filter: grayscale(100%) opacity(50%);
  }
  .clients__img:hover {
    filter: none;
  }
}

@media only screen and (max-width: 850px) {
  .clients__img {
    padding: 30px 0px;
  }
}

@media only screen and (max-width: 530px) {
  .clients__img {
    width: 35%;
  }
}

@media only screen and (max-width: 420px) {
  .clients__img {
    width: 60%;
  }
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  display: inline-block;
  width: 40%;
  min-width: 300px;
  padding-bottom: 20%;
  background: #1c7fff;
  border-radius: 10px;
  text-align: right;
  visibility: hidden;
  opacity: 0;
  z-index: -100;
  transform: translate(-50%, -200%);
}

.popup--open {
  visibility: visible;
  opacity: 1;
  z-index: 100;
  transform: translate(-50%, -50%);
  transition: transform 0.5s ease;
}

.popup__close {
  display: inline-block;
  font-size: 50px;
  padding: 2px;
  margin: 0 20px 20px 0;
  cursor: pointer;
  color: #ffff;
}

.popup__title {
  font-family: roboto;
  color: #fff;
  font-size: 40px;
  text-align: center;
}

@media only screen and (max-width: 800px) {
  .popup__title {
    font-size: 30px;
  }
}
